import React from "react";
import './GridImageThumbnail.css';
import './ImageRenderer.css';

class GridRenderer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            image: props.image,
            photographer: props.photographer,
            title: props.title
        }
    }

    render() {
        return (
            <>
                <div className="grid">
                    <div className="grid-image-overlay">
                        <span className="text-overlay">&#169; {this.state.photographer}</span>
                    </div>
                    <img
                        src={"https://beta.oneeyeland.com/" + this.state.image}
                        width={250}
                        height={250}
                        alt={this.state.title}
                        className="grid-image p-1"
                    />
                </div>
            </>
        );
    }
}

export default GridRenderer;
