import React from "react";

class StaticImage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            image: props.image,
            height: props.height,
            width: props.width
        }
    }

    render() {
        return (
            <>
                <img src={this.state.image} alt={"static file"} height={this.state.height} width={this.state.width}/>
            </>
        )
    }
}

export default StaticImage;
