import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import '../PreselectImages.css';
import RegisterModal from '../../PaidImages/Components/Register';


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  height:700,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function BasicModal() {
  const [usermail,setusermail]=React.useState('')
  const [password,setpassword]=React.useState('')
  const [checkbox,setcheckbox]=React.useState('')
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button onClick={handleOpen}>LOGIN</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            <h2 className='heading-box-login'>ACCOUNT LOGIN</h2>
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            <div className="buttons-google-facebook-twiiter">
                <button className='facebook'>LOGIN WITH FACEBOOK</button>
                <button className='google'>LOGIN WITH GOOGLE</button>
                <button className='twitter'>LOGIN WITH TWITTER</button>
                <p>OR</p>
            </div>
            
            <form action="/api/auth/signin" method='post' className='register-form'>
                <input className='username-login' type="text" onChange={(event)=>{setusermail(event.target.value)}} placeholder="Username" />
                <input type="password" onChange={(event)=>{setpassword(event.target.value)}}  placeholder='Password' />
                
                <div className="checkbox-content">
                <input  type="checkbox" name="keepmesignedin" id="keepmesignedin" onChange={(event)=>{setcheckbox(event.target.checked)}} />
                <p>Keep me logged in</p>
                </div>
                <button type='submit'>LOG IN</button>
                <button>FORGOT PASSWORD</button>
                <RegisterModal />

            </form>
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}
