import { textAlign } from '@mui/system';
import React from 'react';
import { useTimer } from 'react-timer-hook';
import '../Info.css';

const MyTimer=({ expiryTimestamp })=> {
    const {
      seconds,
      minutes,
      hours,
      days,
      isRunning,
      start,
      pause,
      resume,
      restart,
    } = useTimer({ expiryTimestamp, onExpire: () => console.warn('onExpire called') });
  
  
    return (
      <div className='timer-box' style={{textAlign: 'center'}}>
        <h1 >FINAL DEADLINE (NO EXTENSIONS)</h1>
        <div className='timer-font' style={{fontSize: '25px'}}>
         <span>{`${days}:`}</span><span>{hours}</span>:<span>{minutes}</span>:<span>{seconds.toString().length==1 ? `0${seconds}`: seconds} </span>
            {/*<p className='days'>DAYS</p>
        <p className='hour'>HRS</p>
        <p className='min'>MIN</p>
        <p className='sec'>SEC</p>*/}
        </div>
      </div>
    );
  }

export default MyTimer;