import React from "react";
import './ImageRenderer.css'

class ImageRenderer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            src: props.src,
            height: props.height,
            photographer: props.photographer,
            width: props.width,
            text: props.text,
            kind: props.kind,
            status: props.status,
            invert: props.hasOwnProperty("invert") ? props.invert : false
        }
    }

    render() {
        if (!Array.isArray(this.state["src"])) {
            // console.log("Rendering: " + this.state["src"] + " for " + this.state.text)
            const statusClass = this.state.hasOwnProperty("invert") && this.state.invert === true ? "top100-status" : "oe-success"
            if (this.state.kind === "contest") {
                return (
                    <div style={{width:"100%"}}>
                        <div className="homepage-image-container" style={{width:"100% !important"}} >
                            <img src={this.state["src"]}
                                 alt={this.state.text} className={"homepage-image img-fluid"} style={{width:"100%"}} />
                            <div className="status-overlay"><span className={statusClass}>{this.state.status}</span></div>
                            <div className="pg-overlay"><span className="pg-overlay-text">{this.state.photographer}</span></div>
                        </div>
                    </div>
                )
            } else if (this.state.kind === "selection") {
                // console.log(this.state)
                return (
                    <>
                        <div className="selection-image-container">
                            <img src={this.state["src"]}
                                 alt={this.state.text} className={"homepage-image img-fluid"}/>
                            <div className="status-overlay"><span className="oe-success">{this.state.status}</span></div>
                            <div className="pg-overlay"><span className="pg-overlay-text">{this.state.photographer}</span></div>
                        </div>
                    </>
                )
            }
        } else {
            return (
                <>
                    <div className="selection-image-container">
                        <div className="selection-grid">
                        {
                            this.state["src"].map((src, idx) => {
                                // console.log(this.state.kind)
                                console.log("Arrary src")
                                console.log(src)
                                if (this.state.kind === "contest") {
                                    return (
                                        <>
                                            <div className="overlay-container">
                                                <img src={src} width={this.state.width} height={this.state.height}
                                                     alt={this.state.text} className="img-fluid"/>
                                                <div className="inner-overlay"><span className="inner-overlay-text">{this.state.overlay}</span></div>
                                            </div>
                                        </>
                                    )
                                } else if (this.state.kind === "selection") {
                                    // console.log(src)
                                    return (
                                        <>
                                            <div className="overlay-container">
                                                <img src={src}
                                                     alt={this.state.text} className="selection-image img-fluid"/>
                                                <div className="inner-overlay"><span className="inner-overlay-text">{src.photographer}</span></div>
                                            </div>
                                        </>
                                    )

                                } else {
                                    // console.log("Undefined kind")
                                    return (
                                        <></>
                                    )
                                }
                            })
                        }
                            <div className="gallery-status-overlay"><span className="oe-success">CURATED PHOTO GALLERY</span></div>
                        </div>
                    </div>
                </>
            )
        }
    }
}

export default ImageRenderer;
