import React from "react";
import {useParams} from "react-router-dom";
import {Container, Row} from "react-bootstrap";
import StaticImage from "../StaticFile/StaticImage";
import Gallery from "react-grid-gallery";


const withParams = (Comp) => {
    return props => <Comp {...props} params={useParams()} />;
}



class GalleryOverlay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            imgId: this.props.params.imgId,
            galleryType: this.props.params.galleryType,
            image: {},
            photographer: {},
            previous: 0,
            next: 0,
            title: "",
            loaded: false,
            clipboardClass: "bi bi-clipboard2"
        }

        this.iconMap = {
            facebook: "bi bi-facebook  p-2 fs-5",
            instagram: "bi bi-instagram p-2 fs-5",
            twitter: "bi bi-twitter p-2 fs-5",
            website: "bi bi-link-45deg p-2 fs-5",
            pinterest: "bi bi-pinterest p-2 fs-5",
            behance: "bi bi-behance p-2 fs-5",
            linkedin: "bi bi-linkedin p-2 fs-5",
        }

        this.urlMap = {
            facebook: "https://www.facebook.com",
            instagram: "https://www.instagram.com",
            twitter: "https://www.twitter.com"
        }
    }

    componentDidMount() {
        console.log(this.state.imgId)
        if (this.state.imgId !== 0) {
            this.fetchImage(this.state.imgId)
        }
    }

    fetchImage(imgId) {
        if (this.state.imgId !== imgId) {
            console.log("Updating image id")
            this.setState({"imgId": imgId})
        }
        const url = "/api/images/gallery/" + this.state.galleryType + "/" + imgId
        fetch(url)
            .then((res) => res.json())
            .then((resJson) => {
                if (resJson.error === false) {
                    this.setState({
                        image: resJson.response.image,
                        photographer: resJson.response.photographer,
                        previous: resJson.response.previous,
                        next: resJson.response.next,
                        title: resJson.response.title
                    })
                    this.setState({loaded: true})
                } else {
                    console.log("Failed to fetch image")
                }
            })
            .catch((err) => console.log(err))
        this.setState({clipboardClass: "bi bi-clipboard2"})

    }

    fetchPrevious() {
        console.log("Fetching image: " + this.state.previous)
        this.fetchImage(this.state.previous)
    }

    fetchNext() {
        console.log("Fetching image: " + this.state.next)
        this.fetchImage(this.state.next)
    }

    copyUrl() {
        navigator.clipboard.writeText("https://beta.oneeyeland.com/" + this.state.image)
            .then((res) => console.log(res) )
        this.setState({clipboardClass: "bi bi-clipboard2-check"})
    }
    render() {
        if (this.state.loaded === false) {
            return (
                <>
                    <Row>
                        <Container>
                            <div className="d-inline-block">
                                <StaticImage image={"/loader.gif"} height={500} width={500}/>
                            </div>
                        </Container>
                    </Row>
                </>
            )
        } else {
            return (
              <>
                {/* <Container className="container-fluid d-flex flex-column align-items-center"> */}
                   <Container className="mt-5 py-2">
                  <Row>
                    <div className="d-flex justify-content-between">
                      <button onClick={this.fetchPrevious.bind(this)}>
                        <i
                          className="bi bi-arrow-left-circle-fill"
                          style={{ fontSize: "60px" }}
                        ></i>
                      </button>
                      {/* <div data-bs-toggle="tooltip" data-bs-placement="right" title={this.state.title}>
                                <img src={this.state.image} alt={this.state.title}/>
                            </div> */}
                      <div
                        data-bs-toggle="tooltip"
                        data-bs-placement="right"
                        title={this.state.title}
                      >
                        <Gallery
                          src={this.state.image}
                          alt={this.state.title}
                        />
                      </div>
                      <button onClick={this.fetchNext.bind(this)}>
                        <i
                          className="bi bi-arrow-right-circle-fill"
                          style={{ fontSize: "60px" }}
                        ></i>
                      </button>
                    </div>
                  </Row>
                  <Row>
                    <div className="py-4">
                      <div className="d-flex justify-content-center">
                        <span className="pgname px-4">
                          <strong>{this.state.photographer.name}</strong>
                        </span>
                        <>
                          {Object.entries(this.state.photographer.social).map(
                            (entry, idx) => {
                              const [k, v] = entry;
                              if (v !== null && v.length > 2) {
                                return (
                                  <>
                                    <span className="px-2">
                                      <a href={this.urlMap[k] + "/" + v}>
                                        <i className={this.iconMap[k]}></i>
                                      </a>
                                    </span>
                                  </>
                                );
                              } else {
                                return (
                                  <>
                                    <span></span>
                                  </>
                                );
                              }
                            }
                          )}
                        </>
                        <span className="px-2">
                          <i
                            className={this.state.clipboardClass}
                            onClick={this.copyUrl.bind(this)}
                          ></i>
                        </span>
                      </div>
                    </div>
                  </Row>
                </Container>
              </>
            );
        }
    }

}

export default withParams(GalleryOverlay);