import React from "react";
import {useParams} from "react-router-dom";
import {Container, Row} from "react-bootstrap";
import StaticImage from "../../components/StaticFile/StaticImage";
import GalleryImageThumbnail from "../../components/ImageRenderer/GalleryImageThumbnail";
import SimpleTitle from "../../components/TitleWidget/SimpleTitle";
import './CuratedGallery.css'

const withParams = (Comp) => {
    return props => <Comp {...props} params={useParams()} />;
}

class PrintsView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            baseUrl: props.url,
            title: props.title,
            images: [],
            thumbnails: [],
            photographers: [],
            data: [],
            currentIndex: 0,
            pgId: props.params.pgId
        }
        this.fetched = 1
        this.photographers = []
        this.photographerRef = React.createRef()
        this.leftChevronRendered = false
    }

    componentDidMount() {
        const url = this.state.baseUrl + "/" + this.fetched
        fetch("/api/images/prints/photographers/list")
            .then((res) => res.json())
            .then((resJson) => {
                if (resJson.error === false) {
                    console.log("Photographers fetched")
                    const photographersList = [{id: "all", name: "All", email: "all"}]
                    resJson.response.map((pg, idx) => photographersList.push(pg));
                    this.setState({photographers: photographersList})
                } else {
                    console.log(resJson.response.message)
                }
            })
            .catch((err) => console.log(err));
        this.fetchImages(url)
        this.fetched += 1;
        /*
        this.state.thumbnails.add({image: "public/right-arrow.jpg", overlay: "next"})
        if (this.state.rendered > 50) {
            this.state.thumbnails.insert(0, {image: "public/right-arrow.jpg", overlay: "previous"})
        }

         */
    }

    fetchImages(url) {
        const firstImage = {
            image: "https://oneeyeland.com/gallery_limited/images/products/small-777fd17cb8a1fc375be9ac368cb0e2cc.jpg?v=0.1",
            title: "Ganesha - Remover of obstacles - India",
            target: ""
        }
        const secondImage = {
            image: "https://oneeyeland.com/gallery_limited/images/products/small-c62504affa30b51fcbd150409cb3be3f.jpg?v=0.1",
            title: "Lakshmi - Hindu Goddess of wealth, India",
            target: ""
        }
        let thumbNails = []
        // console.log(this.fetched)
        for (let i = 0; i <50; i++) {
            if (this.fetched <= 50) {
                thumbNails.push(firstImage)
            } else {
                thumbNails.push(secondImage)
            }
        }
        fetch(url)
            .then((res) => res.json())
            .then((resJson) => {
                if (resJson.error === true) {
                    console.log(resJson.response.message)
                } else {
                    this.setState({data: resJson.response})
                }
            })
            .catch((err) => {
                console.log(err);
                this.setState({
                    thumbnails: thumbNails
                })
            })
    }

    fetchNext() {
        this.leftChevronRendered = false
        const photographer = this.photographerRef.current.value;
        if (photographer === "all") {
            const url = this.state.baseUrl + "/" + this.fetched
            this.fetchImages(url)
            this.fetched += 1;
        } else {
            const url = "/api/images/prints/" + photographer + "/" + this.fetched
            this.fetchImages(url)
            this.fetched += 1;
        }
        this.photographerRef.current.value = photographer
    }

    fetchPrevious() {
        const photographer = this.photographerRef.current.value
        if (this.fetched > 2) {
            this.fetched -= 1;
            this.leftChevronRendered = false
            if (photographer === "all") {
                const url = this.state.baseUrl + "/" + this.fetched
                this.fetchImages(url)
            } else {
                const url = "/api/images/prints/" + photographer + "/" + this.fetched
                this.fetchImages(url)
            }
        }
        this.photographerRef.current.value = photographer
    }

    resetState() {
        this.fetched = 1
        this.leftChevronRendered = false
    }
    handlePhotographerChange = () => {
        const photographer = this.photographerRef.current.value
        const photographers = this.state.photographers
        const url = "/api/images/prints/" + photographer + "/" + this.fetched
        this.setState({data: []})
        this.resetState()
        fetch(url)
            .then((res) => res.json())
            .then((resJson) => {
                if (resJson.error === true) {
                    console.log(resJson.response.message)
                } else {
                    this.setState({data: resJson.response})
                }
            })
            .catch((err) => console.log(err))
        this.setState({photographers: photographers})
    }

    render() {
        if (this.state.data.length === 0) {
            return (
                <>
                    <Row>
                        <Container>
                                <div className="d-inline-block">
                                    <StaticImage image={"/loader.gif"} height={500} width={500}/>
                                </div>
                        </Container>
                    </Row>
                </>
            )
        } else {
            console.log(this.fetched)
            return (
                <>
                    <Row>
                        <div className="col-lg-3 col-md-6 col-sm-12 col-xs-12 col-xl-1">
                            <select name={"photographer"} id={"photographer"}  ref={this.photographerRef} onChange={this.handlePhotographerChange}>
                                {
                                    this.state.photographers.map((photographer, idx) => {
                                        return (
                                            <>
                                                <option value={photographer.email} key={idx}>{photographer.name}</option>
                                            </>
                                        )
                                    })
                                }
                            </select>
                        </div>
                    </Row>
                    <Row>
                        <Container fluid>
                        <div className="row d-flex justify-content-center image-grid gy-1 pt-3">
                            {
                                this.state.data.map((thumbnail, idx) => {
                                    if (this.fetched > 2 && !this.leftChevronRendered) {
                                        this.leftChevronRendered = true
                                        return (
                                            <div className="col-auto image-grid-item icon-large nav-link">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="100" height="240"
                                                     fill="currentColor" className="bi bi-chevron-left"
                                                     viewBox="0 0 16 16" onClick={this.fetchPrevious.bind(this)}>
                                                    <path fillRule="evenodd"
                                                          d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>
                                                </svg>
                                            </div>

                                        )
                                    }
                                    return (
                                        <>
                                            <div className="col-auto image-grid-item border m-2 p-1 ">
                                                <GalleryImageThumbnail  image={thumbnail.image}
                                                                       metadata={thumbnail.overlay}
                                                                       key={thumbnail.image} />
                                            </div>
                                        </>
                                    )
                                })
                            }
                            <div className="col-auto image-grid-item icon-large nav-link">
                                <svg xmlns="http://www.w3.org/2000/svg" width="100" height="240" fill="currentColor"
                                     className="bi bi-chevron-right" viewBox="0 0 16 16" onClick={this.fetchNext.bind(this)}>
                                    <path fillRule="evenodd"
                                          d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/>
                                </svg>
                            </div>
                        </div>
                    </Container>
                    </Row>
                </>
            )
        }
    }
}

// export default GalleryView;
export default withParams(PrintsView);