import {NavDropdown} from "react-bootstrap";
import React from "react";
import './Navigation.css'
import {Link} from "react-router-dom";
import { Dropdown } from "rsuite";
import DropdownItem from "rsuite/esm/Dropdown/DropdownItem";

class DropDownMenuItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            title: props.title,
            link: props.link,
            children: props.children,
            level: props.level
        }
    }

    render() {
        return (
          <>
            <div 
              className="nav-item menu-head m-2" 
              onMouseLeave={this.close}
            >
              
                    {/* <NavDropdown title={this.state.title} key={this.state.title}> */}
                <Dropdown title={this.state.title} key={this.state.title} >
                  {this.state.children.map((child, idx) => {
                    if (child.hasOwnProperty("submenu")) {
                      return (
                        <>
                          <DropDownMenuItem
                            title={child.title}
                            children={child.submenu}
                            level={this.state.level + 1}
                            link={this.state.link + "/" + child.link}
                            key={child.title}
                          />

                          {/* <Dropdown.Menu
                            title={child.title}
                            // children={child.submenu}
                            // level={this.state.level + 1}
                            // link={this.state.link + "/" + child.link}
                            // key={child.title}
                          /> */}

                        </>
                      );
                    } else {
                      const target = this.state.link + "/" + child.link;
                      return (
                        <>
                          <Dropdown.Item
                          // div className="link-wrap nav-item nav-wide p-2 m-2"
                          >
                            <Link
                                to={target}
                                key={child.title}
                                style={{
                                  textDecoration: "none",
                                  display: "block",
                                  color: "grey",
                                }}
                              >
                                {child.title}
                              </Link>
                            {/* <a
                              href={target}
                              key={child.title}
                              style={{
                                textDecoration: "none",
                                display: "block",
                                color: "gray",
                              }}
                            >
                              {child.title}
                            </a> */}
                          </Dropdown.Item>
                        </>
                      );
                    }
                  })}
                {/* </NavDropdown> */}
                </Dropdown>
              
            </div>
          </>
        );


    }

    open (e) {
      if (!e.target.id.startsWith('menubutton-')) {
        return;
      }
      e.target.click();
    }

    close (e) {
      if (e.target.getAttribute('aria-expanded') === 'true' || e.target.parentElement.getAttribute('aria-expanded') === 'true' || e.target.parentElement.parentElement.getAttribute('aria-expanded') === 'true') {
        e.target.click();
      }
    }


}
