import * as React from 'react';
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import '../Presentation.css';
//import AnchorLink from "react-anchor-link-smooth-scroll";
import Page from './Page';

function LinkTab(props) {
  return (
    <Tab

      label={props.label}
      onClick={(event) => {
        event.preventDefault();
      }}
      {...props}

    />
  );
}

export default function NavTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

/*        <!--<AnchorLink href="#Home"><LinkTab label="HOME" /></AnchorLink>
    <AnchorLink href="#Ebook"><LinkTab label="EBOOK"  /></AnchorLink>
    <AnchorLink href="#DistributedList"><LinkTab label="DISTRIBUTION LIST" /></AnchorLink>
    <AnchorLink href="#Price"><LinkTab label="PRICING" /></AnchorLink>
    <AnchorLink href="#Size"><LinkTab label="BOOK SIZE"/></AnchorLink>
    <AnchorLink href="#Submit"><LinkTab label="SUBMIT"/></AnchorLink>
    -->

 */
  return (
    <Box sx={{ width: '100%' }}>
      <Tabs value={value} onChange={handleChange} aria-label="nav tabs example">
         <Tab label={"HOME"} id={"simple-tab-0"} aria-controls={"simple-tab-panel-0"}></Tab>
         <Tab label={"EBOOK"} id={"simple-tab-1"} aria-controls={"simple-tab-panel-1"}></Tab>
         <Tab label={"DISTRIBUTION LIST"} id={"simple-tab-2"} aria-controls={"simple-tab-panel-2"}></Tab>
         <Tab label={"PRICING"} id={"simple-tab-3"} aria-controls={"simple-tab-panel-3"}></Tab>
         <Tab label={"BOOK SIZE"} id={"simple-tab-4"} aria-controls={"simple-tab-panel-4"}></Tab>
         <Tab label={"SUBMIT"} id={"simple-tab-5"} aria-controls={"simple-tab-panel-5"}></Tab>
      </Tabs>
    </Box>
  );
}
