import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData('1', '0% ~ US$ 500', '15% ~ US$ 425', '25% ~ US$ 375'),
  createData('2', '5% ~ US$ 475','20% ~ US$ 400', '25% ~ US$ 375'),
  createData('3', '10% ~ US$ 450','25% ~ US$ 375', '25% ~ US$ 375'),
  createData('4', '15% ~ US$ 425', '25% ~ US$ 375', '25% ~ US$ 375'),
  createData('5', '20% ~ US$ 400', '25% ~ US$ 375', '25% ~ US$ 375'),
  createData('6 or more', '25% ~ US$ 375', '25% ~ US$ 375', '25% ~ US$ 375'),
];

export default function BasicTable() {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>NO.OF PAGES</TableCell>
            <TableCell align="right">MEMBER COUNT ~ COST PER PAGE</TableCell>
            <TableCell align="right">PLAN 25 DISCOUNT ~ COST PER PAGE</TableCell>
            <TableCell align="right">PLAN UNLTD DISCOUNT ~ COST PER PAGE(g)</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="right">{row.calories}</TableCell>
              <TableCell align="right">{row.fat}</TableCell>
              <TableCell align="right">{row.carbs}</TableCell>
              <TableCell align="right">{row.protein}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
