import React from "react";
import {Link} from "react-router-dom";
import './Navigation.css'


class MenuItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            href: props.href,
            title: props.title
        }
    }

    render() {
        console.log(this.state.title)
        if (this.state.href === null) {
            this.setState({href: "/"})
        }
        return (
            <>
                <div className="nav-item p-2 m-2">
                    <Link to={this.state.href} style={{textDecoration: 'none', display: 'block', color: 'gray'}}>{this.state.title}</Link>
                </div>
            </>
        )
    }
}

export default MenuItem;
