import React, { useState, useRef, useEffect } from "react";
import ReactModalLogin from "react-modal-login";
import "react-modal-login/dist/react-modal-login.css";
import HCaptcha from "@hcaptcha/react-hcaptcha";
import Login from "./Captcha";
import Homepage from "./Home/Home";
import Captcha from "./Captcha";










const facebook = {
  appId: "YOUR FB APP ID GOES HERE",
  cookie: true,
  xfbml: true,
  version: "v12.0",
  scope: "email",
  return_scopes: false,
};

export const facebookConfig = facebook;


const google = {
  client_id: "YOUR_CLIENT_ID.apps.googleusercontent.com",
  scope: "profile email",
};

export const googleConfig = google;




const LoginModal = (props) => {
  const [state, setState] = useState({
    showModal: true,
    loggedIn: null,
    loading: false,
    error: null,
    initialTab: null,
    recoverPasswordSuccess: null,
  });

  const onLogin = async (e, a, c) => {
    console.log(e, a, c);
    console.log("onLogin()");
    console.log("email: " + document.querySelector("#email").value);
    console.log("password: " + document.querySelector("#password").value);

    const email = document.querySelector("#email").value;
    const password = document.querySelector("#password").value;

    if (!email || !password) {
      setState({
        error: true,
      });
    } else {
      let formData = new FormData();
      formData.append("username", email);
      formData.append("password", password);
      fetch("/api/auth/signin", {body: formData, method: "post"})
          .then((res) => res.json());

      onLoginSuccess("form");
    }
  };

  const onRegister = () => {
    console.log("onRegister()");
    console.log("login: " + document.querySelector("#login").value);
    console.log("email: " + document.querySelector("#email").value);
    console.log("password: " + document.querySelector("#password").value);

    const login = document.querySelector("#login").value;
    const email = document.querySelector("#email").value;
    const password = document.querySelector("#password").value;

    if (!login || !email || !password) {
      setState({
        error: true,
      });
    } else {
      // onLoginSuccess("form");
      // return(
        // <Homepage />
        <Captcha />
        // <a href="captcha"></a>
        // alert('Hello')

      
    }
  };

  const onRecoverPassword = () => {
    console.log("onRecoverPassword()");
    console.log("email: " + document.querySelector("#email").value);

    const email = document.querySelector("#email").value;

    if (!email) {
      setState({
        ...state,
        error: true,
        recoverPasswordSuccess: false,
      });
    } else {
      setState({
        ...state,
        error: null,
        recoverPasswordSuccess: true,
      });
    }
  };

  const openModal = (initialTab) => {
    console.log("openModal()");

    setState({
      ...state,
      initialTab: initialTab,
      showModal: true,
    });
  };

  const onLoginSuccess = (method, response) => {
    console.log("onLoginSuccess()");

    setState({
      ...state,
      showModal: false,
      error: null,
      loggedIn: method,
      loading: false,
    });
  };

  const onLoginFail = (method, response) => {
    console.log("onLoginFail()");
    setState({
      ...state,
      loading: false,
      error: response,
    });
  };

  const startLoading = () => {
    console.log("startLoading()");
    setState({
      ...state,
      loading: true,
    });
  };

  const finishLoading = () => {
    console.log("finishLoading()");
    setState({
      ...state,
      loading: false,
    });
  };

  const afterTabsChange = () => {
    console.log("afterTabsChange()");

    setState({
      ...state,
      error: null,
      recoverPasswordSuccess: false,
    });
  };

  const closeModal = () => {
    console.log("closeModal()");
    setState({
      ...state,
      showModal: false,
      error: null,
      loading: false,
    });
  };

  const loggedIn = state.loggedIn ? (
    <div>
      <p>You are signed in with: {state.loggedIn}</p>
    </div>
    // <Redirect to="/somewhere/else" />
  ) : (
    // alert("Login Failed")
    <p></p>
  );

  const isLoading = state.loading;

  const [token, setToken] = useState(null);
  const captchaRef = useRef(null);

  const onLoad = () => {
    // this reaches out to the hCaptcha JS API and runs the
    // execute function on it. you can use other functions as
    // documented here:
    // https://docs.hcaptcha.com/configuration#jsapi
    captchaRef.current.execute();
  };

  useEffect(() => {

    if (token)
      console.log(`hCaptcha Token: ${token}`);

  }, [token]);

  return (
    <div className="my-5">
      {/* <button className="RML-btn" onClick={() => openModal("login")}>
        Login
      </button> */}
      <div className=" border container-lg text-center  p-5 d-flex flex-column justify-content-center">
        <figure class="text-center">
          <blockquote class="blockquote">
            <p>
              Photography is a way of feeling, of touching, of loving. What you
              have caught on film is captured forever It remembers little
              things, long after you have forgotten everything.
            </p>
          </blockquote>
          <figcaption class="blockquote-footer">
            <cite title="Source Title">Aaron Siskind</cite>
          </figcaption>
        </figure>
        <h5 className="p-4">Click below to register!</h5>
        <button
          className="RML-btn text-center "
          onClick={() => openModal("register")}
        >
          <p class="lead">Register / Login</p>
        </button>
      </div>

      {/* <form>
        <HCaptcha
          sitekey="cdb856c6-d60e-42d6-a66a-f27e0d7dbe5e"
          onLoad={onLoad}
          onVerify={setToken}
          ref={captchaRef}
        />
      </form> */}

      <ReactModalLogin
        visible={state.showModal}
        onCloseModal={closeModal}
        loading={isLoading}
        initialTab={state.initialTab}
        error={state.error}
        tabs={{
          afterChange: afterTabsChange,
        }}
        startLoading={startLoading}
        finishLoading={finishLoading}
        form={{
          onLogin: onLogin,
          onRegister: onRegister,
          onRecoverPassword: onRecoverPassword,

          recoverPasswordSuccessLabel: state.recoverPasswordSuccess
            ? {
                label: "New password has been sent to your mailbox!",
              }
            : null,
          recoverPasswordAnchor: {
            label: "Forgot your password?",
          },
          loginBtn: {
            label: "Sign in",
          },
          registerBtn: {
            label: "Sign up",
          },
          recoverPasswordBtn: {
            label: "Send new password",
          },
          loginInputs: [
            {
              containerClass: "RML-form-group",
              label: "Email",
              type: "email",
              inputClass: "RML-form-control",
              id: "email",
              name: "email",
              placeholder: "Email",
            },
            {
              containerClass: "RML-form-group",
              label: "Password",
              type: "password",
              inputClass: "RML-form-control",
              id: "password",
              name: "password",
              placeholder: "Password",
            },
          ],
          registerInputs: [
            {
              containerClass: "RML-form-group",
              label: "First name",
              type: "text",
              inputClass: "RML-form-control",
              id: "login",
              name: "login",
              placeholder: "First name",
            },
            {
              containerClass: "RML-form-group",
              label: "Last name",
              type: "text",
              inputClass: "RML-form-control",
              id: "login",
              name: "login",
              placeholder: "Last name",
            },
            {
              containerClass: "RML-form-group",
              label: "Username",
              type: "text",
              inputClass: "RML-form-control",
              id: "login",
              name: "login",
              placeholder: "Username",
            },
            {
              containerClass: "RML-form-group",
              label: "Email",
              type: "email",
              inputClass: "RML-form-control",
              id: "email",
              name: "email",
              placeholder: "Email",
            },
            {
              containerClass: "RML-form-group",
              label: "Password",
              type: "password",
              inputClass: "RML-form-control",
              id: "password",
              name: "password",
              placeholder: "Password",
            },
          ],
          recoverPasswordInputs: [
            {
              containerClass: "RML-form-group",
              label: "Email",
              type: "email",
              inputClass: "RML-form-control",
              id: "email",
              name: "email",
              placeholder: "Email",
            },
          ],
        }}
        separator={{
          label: "or",
        }}
        providers={{
          facebook: {
            config: facebookConfig,
            onLoginSuccess: onLoginSuccess,
            onLoginFail: onLoginFail,
            inactive: isLoading,
            label: "Continue with Facebook",
          },
          google: {
            config: googleConfig,
            onLoginSuccess: onLoginSuccess,
            onLoginFail: onLoginFail,
            inactive: isLoading,
            label: "Continue with Google",
          },
        }}
      />
      {loggedIn}
    </div>
  );
};

export default LoginModal;
