import React from "react";
import {Container, Row} from "react-bootstrap";
import StaticImage from "../../components/StaticFile/StaticImage";
import SimpleTitle from "../../components/TitleWidget/SimpleTitle";
import './CuratedGallery.css'
import {useParams, useNavigate, Link} from "react-router-dom";
import GridRenderer from "../../components/ImageRenderer/GridRenderer";


const withParams = (Comp) => {
    return props => <Comp {...props} params={useParams()} />;
}





class GalleryView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            baseUrl: props.url,
            galleryType: props.galleryType,
            data: [],
            images: [],
            thumbnails: [],
            currentIndex: 1,
            title: props.title,
            navigation: props.navigation,
            activeImageId: 0,
            domElements: [],
            gallery: {
                name: "Gallery",
                options: [
                    {name: "Curated Photo Gallery", value: "gallery"},
                    {name: "Stock Images", value: "stock"},
                    {name: "Picture of the day", value: "pic-of-the-day"},
                    {name: "Picture of the week", value: "pic-of-the-week"},
                    {name: "Photographer of the week", value: "photographer-of-the-week"},
                    {name: "Photographer of the month", value: "photographer-of-the-month"}
                ]
            },
            category: {
                name: "Category",
                options: [
                    {name: "All", value: "all"},
                    {name: "Action", value: "action"},
                    {name: "Aerial", value: "aerial"},
                    {name: "Animals", value: "animals"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                    {name: "All", value: "all"},
                ]
            },
            country: {
                name: "Country",
                options: [
                    {name: "India", value: "india"},
                    {name: "Australia", value: "australia"},
                    {name: "UK", value: "uk"},
                    {name: "USA", value: "usa"},
                ]

            },
            search: {
                name: "Search",
                options: [
                    {name: "Search", value: "all"},
                    {name: "Search by title", value: "title"},
                    {name: "Search by photographer", value: "photographer"},
                ]
            }
        }
        this.overlayRef = React.createRef();
        this.fetched = 1
        this.fetchComplete = false
        this.leftChevronRendered = false
        this.rightChevronRendered = false
        this.galleryTypeRef = React.createRef();
        this.countryRef = React.createRef();
        this.categoryRef = React.createRef();
        this.searchRef = React.createRef();
        this.country = "all"
        this.category = "all"
        this.search = ""
        this.domElements = []
        console.log(this.state.galleryType)
    }

    componentDidMount() {
        const url = this.state.baseUrl + "/page/" + this.fetched
        fetch("/api/common/countries")
            .then((res) => res.json())
            .then((resJson) => {
                let countries = [{name: "All", value: "all"}]
                resJson.response.map((country, idx) => {
                    countries.push({name: country.display_name, value: country.iso3})
                })
                let res = {name: "Country", options: countries}
                this.setState({country: res})
            })
            .catch((err) => console.log(err))
        fetch("/api/images/categories/list")
            .then((res) => res.json())
            .then((resJson) => {
                let cats = [{name: "All", id: "all"}]
                resJson.response.map((cat, idx) => {
                    cats.push(cat)
                })
                let res = {name: "Category", options: cats}
                this.setState({category: res})
            })
            .catch((err) => console.log(err))

        this.fetchImages(url)
        this.fetched += 1;
        /*
        this.state.thumbnails.add({image: "public/right-arrow.jpg", overlay: "next"})
        if (this.state.rendered > 50) {
            this.state.thumbnails.insert(0, {image: "public/right-arrow.jpg", overlay: "previous"})
        }

         */
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    openOverlay = (imgId) => {
        const overlayNode = this.overlayRef.current
        overlayNode.style.width = "100%";
        console.log("Updating active Image: " + imgId)
        this.setState({activeImageId: imgId})

    }

    closeOverlay() {
        const overlayNode = this.overlayRef.current
        overlayNode.style.width = "0%";
    }

    galleryTypeChanged = () => {
        const galleryType = this.galleryTypeRef.current.value;
        this.leftChevronRendered = false;
        this.rightChevronRendered = false;
        console.log("Changing type")
        if (galleryType === "gallery") {
            this.state.navigation("/gallery/list");
            this.fetched = 1;
            this.fetchImages("/api/images/gallery/list/page/1")
            this.setState({baseUrl: "/api/images/gallery/list"})
        } else if (galleryType === "stock") {
            this.state.navigation("/gallery/stock");
            this.fetched = 1;
            this.fetchImages("/api/images/stock/list/page/1")
            this.setState({baseUrl: "/api/images/stock/list"})
        } else if (galleryType === "pic-of-the-day") {
            this.state.navigation("/gallery/pic-of-the-day")
            this.fetched = 1;
            this.fetchImages("/api/images/picture-of-the-day/list/page/1")
            this.setState({baseUrl: "/api/images/picture-of-the-day/list"})
        } else if (galleryType === "pic-of-the-week") {
            this.state.navigation("/gallery/pic-of-the-week")
            this.fetched = 1;
            this.fetchImages("/api/images/picture-of-the-week/list/page/1")
            this.setState({baseUrl: "/api/images/picture-of-the-week/list"})
        } else if (galleryType === "photographer-of-the-week") {
            this.state.navigation("/gallery/photographer-of-the-week")
            this.fetched = 1;
            this.fetchImages("/api/images/photographer-of-the-week/list/page/1")
            this.setState({baseUrl: "/api/images/photographer-of-the-week/list"})
        } else if (galleryType === "photographer-of-the-month") {
            this.state.navigation("/gallery/photographer-of-the-month")
            this.fetched = 1;
            this.fetchImages("/api/images/photographer-of-the-month/list/page/1")
            this.setState({baseUrl: "/api/images/photographer-of-the-month/list"})
        }
        this.fetched += 1;
    }

    countryChanged() {
        const country = this.countryRef.current.value;
        const category = this.categoryRef.current.value;
        let search = this.searchRef.current.value;
        if (search.length < 2) {
            search = "all"
        }
        this.fetched = 1;
        this.leftChevronRendered = false
        this.rightChevronRendered = false
        this.fetchImages("/api/images/search/" + this.state.galleryType + "/" + category + "/" + country + "/" + search + "/" + this.fetched)
        this.fetched += 1;
    }

    categoryChanged() {
        const category = this.categoryRef.current.value
        const country = this.countryRef.current.value
        let search = this.searchRef.current.value
        if (search.length < 2) {
            search = "all"
        }
        this.fetched = 1
        this.leftChevronRendered = false
        this.rightChevronRendered = false
        this.fetchImages("/api/images/search/" + this.state.galleryType + "/" + category + "/" + country + "/" + search + "/" + this.fetched)
        this.fetched += 1;
    }

    searchTermTriggered() {
        const category = this.categoryRef.current.value
        const country = this.countryRef.current.value
        let search = this.searchRef.current.value
        if (search.length < 2) {
            search = "all"
        }
        this.fetched = 1
        this.leftChevronRendered = false
        this.rightChevronRendered = false
        const terms = search.split(" ")
        this.fetchImages("/api/images/search/" + this.state.galleryType + "/" + category + "/" + country + "/" + search + "/" + this.fetched)
        this.fetched += 1
    }
    fetchImages(url) {
        const firstImage = {
          image:
            "https://oneeyeland.com/250/462292-one-eyeland-blessings-in-lalibela-by-karen-safer.jpg?v=1",
          overlay: "photo grapher",
          photographer: "John Carter",
        //   socialMedia: {
            facebook: "url",
            instagram: "url",
            xyz: "url",
          
        };
        const secondImage = {
            image: "https://oneeyeland.com/250/462455-one-eyeland-santorini-overlook-by-robb-scharetg.jpg?v=1",
            "overlay": "another pg"
        }
        let thumbNails = []
        this.domElements = []
        // console.log(this.fetched)
        for (let i = 0; i <50; i++) {
            if (this.fetched <= 50) {
                thumbNails.push(firstImage)
            } else {
                thumbNails.push(secondImage)
            }
        }
        fetch(url)
            .then((res) => res.json())
            .then((resJson) => {
                if (resJson.error === true) {
                    console.log(resJson.response.message)
                    this.fetchComplete = true
                } else {
                    this.setState({domElements: []})
                    this.setState({data: resJson.response})
                    this.fetchComplete = true
                    this.populateDom()
                    // this.setState({images: resJson.response.images})
                    // this.setState({thumbnails: resJson.response.thumbnails})
                }
            })
            .catch((err) => {
                console.log(err);
                this.fetchComplete = true
                this.setState({
                    data: thumbNails
                })
            })
    }

    fetchNext() {
        this.leftChevronRendered = false
        this.rightChevronRendered = false
        const category = this.categoryRef.current.value
        const country = this.countryRef.current.value
        const search = this.searchRef.current.value
        if (category === "all" && country === "all" && search === "") {
            const url = this.state.baseUrl + "/page/" + this.fetched
            this.fetchImages(url)
        } else {
            this.fetchImages("/api/images/gallery/" + category + "/" + country + "/" + search + "/" + this.fetched)
        }
        this.fetched += 1;
    }

    fetchPrevious() {
        if (this.fetched > 1) {
            const category = this.categoryRef.current.value
            const country = this.countryRef.current.value
            const search = this.searchRef.current.value
            this.fetched -= 1;
            this.leftChevronRendered = false
            this.rightChevronRendered = false
            if (category === "all" && country === "all" && search === "") {
                const url = this.state.baseUrl + "/page/" + this.fetched
                this.fetchImages(url)
            } else {
                this.fetchImages("/api/images/gallery/" + category + "/" + country + "/" + search + "/" + this.fetched)
            }
        }
    }
    
    populateDom() {
        console.log("Setting up the DOM")
        if (this.fetched > 2 && !this.leftChevronRendered) {
            this.leftChevronRendered = true
            this.domElements.push(
                <div className="col-auto image-grid-item icon-large nav-link p-2" onClick={this.fetchPrevious.bind(this)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="100" height="240"
                         fill="currentColor" className="bi bi-chevron-left"
                         viewBox="0 0 16 16" >
                        <path fillRule="evenodd"
                              d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>
                    </svg>
                </div>
            );
        }
        this.state.data.map((thumbnail, idx) => {
            this.domElements.push(
                <>
                    <div className="col-auto image-grid-item" style={{cursor: "pointer"}}>
                        <Link to={"/image/" + this.state.galleryType + "/" + thumbnail.id}>
                            <GridRenderer image={thumbnail.image}
                                          title={thumbnail.title}
                                          photographer={thumbnail.photographer} />
                        </Link>
                    </div>
                </>
            );
        })
        if (this.domElements.length >= 50  && !this.rightChevronRendered) {
            const target = this.fetched
            this.domElements.push(
                <>
                    <div className="col-auto image-grid-item icon-large nav-link" onClick={this.fetchNext.bind(this)}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="100" height="240" fill="currentColor"
                             className="bi bi-chevron-right" viewBox="0 0 16 16" >
                            <path fillRule="evenodd"
                                  d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/>
                        </svg>
                    </div>
                </>
            )
            this.rightChevronRendered = true
        }
        this.setState({domElements: this.domElements})
        console.log(this.state.domElements)
    }

    render() {
        if (this.state.domElements.length === 0 && this.state.fetchComplete === false) {
            return (
                <>
                    <Row>
                        <Container>
                                <div className="d-inline-block">
                                    <StaticImage image={"/loader.gif"} height={500} width={500}/>
                                </div>
                        </Container>
                    </Row>
                </>
            )
        } else {
            return (
              <div className="align-content-center">
                <Row className="">
                  <SimpleTitle className="" title={this.state.title} />
                </Row>
                <Row>
                  <Container fluid>
                    <div className="  ">
                      <Row>
                        <div
                          className="col-lg-3 col-xs-12 col-sm-12 col-md-6 border  border-start-0"
                          
                        >
                          <div className="form-group">
                            <label
                              id={"lbl-select-gallery"}
                              className="mx-2 pt-1"
                            >
                              GALLERY
                            </label>
                            <select
                              id="gallery-choice"
                              className="form-control form-select form-select-sm "
                              ref={this.galleryTypeRef}
                              onChange={this.galleryTypeChanged}
                              style={{
                                border: "none",
                                backgroundColor: "transparent",
                                resize: "none",
                                outline: "none",
                              }}
                            >
                              {this.state.gallery.options.map((opt, idx) => {
                                return (
                                  <>
                                    <option
                                      value={opt.value}
                                      key={idx}
                                      selected={opt.name === this.state.title}
                                    >
                                      {opt.name}
                                    </option>
                                  </>
                                );
                              })}
                            </select>
                          </div>
                        </div>
                        <div className="col-lg-3 col-xs-12 col-sm-12 col-md-6 border  border-start-0">
                          <div className="form-group">
                            <label
                              id={"lbl-select-category"}
                              className="mx-2 pt-1"
                            >
                              CATEGORY
                            </label>
                            <select
                              id="category-choice"
                              className="form-control form-select form-select-sm "
                              ref={this.categoryRef}
                              onChange={this.categoryChanged.bind(this)}
                              style={{
                                border: "none",
                                backgroundColor: "transparent",
                                resize: "none",
                                outline: "none",
                              }}
                            >
                              {this.state.category.options.map((opt, idx) => {
                                return (
                                  <>
                                    <option value={opt.id} key={idx}>
                                      {opt.name}
                                    </option>
                                  </>
                                );
                              })}
                            </select>
                          </div>
                        </div>
                        <div className="col-lg-3  col-xs-12 col-sm-12 col-md-6 border border-start-0 ">
                          <div className="form-group">
                            <label
                              id={"lbl-select-country"}
                              className="mx-2 pt-1"
                            >
                              COUNTRY
                            </label>
                            <select
                              id="country-choice"
                              className="form-control custom-select form-select form-select-sm"
                              aria-label="Default select example"
                              ref={this.countryRef}
                              onChange={this.countryChanged.bind(this)}
                              style={{
                                border: "none",
                                backgroundColor: "transparent",
                                resize: "none",
                                outline: "none",
                              }}
                            >
                              {this.state.country.options.map((opt, idx) => {
                                return (
                                  <>
                                    <option value={opt.value} key={idx}>
                                      {opt.name}
                                    </option>
                                  </>
                                );
                              })}
                            </select>
                          </div>
                        </div>
                        <div className="col-lg-3 col-xs-12 col-sm-12 col-md-6 border border-start-0 ">
                          <div className="form-group">
                            {/* <span>
                                                <label id={"lbl-search-box"} className="">SEARCH</label>
                                            </span> */}
                            <select
                              id="country-choice"
                              className="form-control custom-select form-select-sm form-select border-none "
                              ref={this.countryRef}
                              onChange={this.countryChanged.bind(this)}
                              style={{
                                border: "none",
                                backgroundColor: "transparent",
                                resize: "none",
                                outline: "none",
                              }}
                            >
                              {this.state.country.options.map((opt, idx) => {
                                return (
                                  <>
                                    <option value={opt.value} key={idx}>
                                      {opt.name}
                                    </option>
                                  </>
                                );
                              })}
                            </select>

                            <div
                              className="input-group input-group-sm    "
                              style={{
                                border: "none",
                                backgroundColor: "transparent",
                                resize: "none",
                                outline: "none",
                              }}
                            >
                              {/* <span
                                id="basic-addon1"
                                className="input-group-text"
                                onClick={this.searchTermTriggered.bind(this)}
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="16"
                                  height="16"
                                  fill="currentColor"
                                  className="bi bi-search"
                                  viewBox="0 0 16 16"
                                >
                                  <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                                </svg>
                              </span> */}
                              <input
                                type="text"
                                name="searchterm"
                                id="searchterm"
                                className="form-control"
                                ref={this.searchRef}
                                placeholder="Enter Search Term"
                              />
                            </div>
                          </div>
                        </div>
                        {/* <div class="input-group  col-lg-3  col-xs-12 col-sm-12 col-md-6">
                          <button
                            class="btn btn-outline-secondary dropdown-toggle"
                            type="button"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                          >
                            Dropdown
                          </button>
                          <ul class="dropdown-menu">
                            <li>
                              <a class="dropdown-item" href="#">
                                Action
                              </a>
                            </li>
                            <li>
                              <a class="dropdown-item" href="#">
                                Another action
                              </a>
                            </li>
                            <li>
                              <a class="dropdown-item" href="#">
                                Something else here
                              </a>
                            </li>
                            <li>
                              <hr class="dropdown-divider" />
                            </li>
                            <li>
                              <a class="dropdown-item" href="#">
                                Separated link
                              </a>
                            </li>
                          </ul>
                          <input
                            type="text"
                            class="form-control"
                            aria-label="Text input with dropdown button"
                          />
                        </div> */}
                      </Row>
                    </div>
                  </Container>
                </Row>
                <Row>
                  <Container fluid>
                    <div className="row d-flex justify-content-center image-grid gy-1 pt-3">
                      {this.state.domElements}
                    </div>
                  </Container>
                </Row>
              </div>
            );
        }
    }
}

export default function(props) {
    const navigation = useNavigate();
    return <GalleryView {...props} navigation={navigation} />;
}
