import React from "react";

class FreeUpload extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            title: props.title,
            logo: props.logo
        }
    }

    render() {
        return (
            <>
                <div className="d-flex flex-row">
                    <div className={this.state.logo}>
                        <p className="ps-5 large d-flex after-logo">{this.state.title}</p>
                    </div>
                </div>
            </>
        )
    }

}

export default FreeUpload;