import React from "react";
import DropDownMenuItem from "./DropDownMenuItem";
import MenuItem from "./MenuItem";
import {MenuData} from "./MenuItemsTest";


class Navigation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            menuItems: []
        }
    }

    componentDidMount() {
        fetch("/api/common/menuitems")
            .then((res) => res.json())
            .then((resJson) => {
		if (resJson.error === false) {
		    this.setState({menuItems: resJson.response});
		} else {
		    console.log("Error fetching menu");
		    this.setState({menuItems: MenuData});
		}
	    })
            .catch((err) => {
                console.log("Using test data");
                this.setState({menuItems: MenuData})
            }
		  )
    }

    render() {
        if (!this.state.menuItems) {
	    console.log("No menus")
            return (
                <> </>
            )
        }
        return (
          <>
            <nav className="navbar navbar-expand-lg navbar-light bg-light navbar-right ">
              <div className="container-fluid">
                  <button
                      className="navbar-toggler"
                      type="button"
                      aria-controls="oeNavbar"
                      aria-expanded="false"
                      aria-label="Toggle navigation"
                      data-bs-toggle="collapse"
                      data-bs-target="#oeNavbar"
                  >
{/*
                  <button
                  className="navbar-toggler"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#oeNavbar"
                  aria-controls="oeNavbar"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
*/}
                  <span className="navbar-toggler-icon"></span>
                </button>
                <a className="navbar-brand" href={"/"}>
                  <img
                    src="/logo.png"
                    width="100"
                    height="52"
                    className="d-inline-flex align-top"
                    alt={"brand logo"}
                  />{" "}
                </a>
                <div className="collapse navbar-collapse" id="oeNavbar">
                    <ul className="navbar-nav ms-auto mb-2  mb-lg-0  top-0 end-0 p-2">
                    {this.state.menuItems.map((menuDefinition) => {
                      // if (menuDefinition.title === "LOGOUT") {
                      //   return(
                      //     <button href='login'>LOGOUT</button>
                      //   )
                      // }
                      if (menuDefinition.hasOwnProperty("submenu")) {
                        return (
                          
                          <DropDownMenuItem
                            title={menuDefinition.title}
                            level="1"
                            link={
                              menuDefinition.hasOwnProperty("link")
                                ? menuDefinition.link
                                : "TBD"
                            }
                            children={menuDefinition.submenu}
                            key={menuDefinition.title}
                          />
                        );
                      } else {
                        return (
                          
                          <MenuItem
                            title={menuDefinition.title}
                            href={menuDefinition.link}
                            variant={
                              menuDefinition.hasOwnProperty("variant")
                                ? menuDefinition.variant
                                : ""
                            }
                            key={menuDefinition.title}
                          />
                        );
                      }
                    })}
                  </ul>
                </div>
              </div>
            </nav>
          </>
        );
        /*
        return (
            <>
                <Navbar bg="light" variant="light">
                    <Container fluid>
                        <Navbar.Brand href="/">
                            <img src="/logo.png" width="100" height="52" className="d-inline-flex align-top" alt="brand logo"/>{'  '}
                        </Navbar.Brand>
                        <Nav className="justify-content-end">
                            {
                                this.state.menuItems.map(menuDefinition => {
                                    if (menuDefinition.hasOwnProperty("submenu")) {
                                        return (
                                            <DropDownMenuItem title={menuDefinition.title}
                                                              link={menuDefinition.hasOwnProperty("link") ? menuDefinition.link : "TBD"}
                                                              children={menuDefinition.submenu} key={menuDefinition.title}/>
                                        )
                                    } else {
                                        return (
                                            <MenuItem title={menuDefinition.title}
                                                      href={menuDefinition.link}
                                                      variant={menuDefinition.hasOwnProperty("variant") ? menuDefinition.variant : ""}
                                                      key={menuDefinition.title}/>
                                        )
                                    }
                                })

                            }
                        </Nav>
                    </Container>
                </Navbar>

            </>
        )

         */
    }


}

export default Navigation;
